<script>
  import questions from "./store";
  import { correct } from "./checkScore";
  import { count, allUnattempt } from "./allAttempt";
  import { createEventDispatcher } from "svelte";
  import { dupliindex, ans } from "./ansStore";
  import escaped from "./escaped";
  const dispatch = createEventDispatcher();
  let questData;
  let total = 11;
  const unsubscribe = questions.subscribe((items) => {
    questData = items;
  });
  export let answ;
  export let dupli;
  dupli = dupliindex;
  answ = ans;
  let incorrect = count - correct;
  let un = allUnattempt.length;
  let indexno1 = null;
  let show = false;
  function showReview(indexno) {
    indexno1 = indexno;
    show = true;
  }
  function previous() {
    if (indexno1 < 1) {
      indexno1 = 0;
    } else {
      indexno1--;
    }
  }
  function next() {
    if (indexno1 > 9) {
      indexno1 = 10;
    } else {
      indexno1++;
    }
  }
  let ajl = null;
  function escapedExplanation(i) {
    ajl = escaped(i);
    return ajl;
  }
</script>

<header>
  <img
    class="img-fluid"
    src="data:image/png;base64,"
    alt="Svelte logo"
  />
  <h1>uCertify Result</h1>
</header>
{#if !show}
  <div class="bodycorrect">
    <div class="incorrectAns">
      <h2>All Items</h2>
      <h4>{total}</h4>
    </div>
    <div class="incorrectAns">
      <h2>Attempted</h2>
      <h4>{total-un}</h4>
    </div>
    <div class="incorrectAns">
      <h2>Unattempted</h2>
      <h4>{un}</h4>
    </div>
    <div class="correctAns">
      <h2>Correct Answer</h2>
      <h4>{correct}</h4>
    </div>
    <div class="incorrectAns">
      <h2>Incorrect Answer</h2>
      <h4>{incorrect}</h4>
    </div>
  </div>

  <h2 class="total">Total Result {correct * 8.5}%</h2>

  <table border="1">
    <tr>
      <th>Index No.</th>
      <th class="questArea">Question</th>
      <th class="ansArea">Answers</th>
      <hr />
    </tr>

    {#each questData as data, i (data)}
      <tr>
        <td>
          <p>{i + 1}</p>
        </td>
        <td class="questArea">
          <a
            href="/"
            on:click|preventDefault={() => showReview(i)}
            style="text-decoration: none;"
            >{JSON.parse(data.content_text).question}</a
          >
        </td>
        <td class="ansArea">
          {#each dupli as ansindex}
            {#if i == ansindex}
              {#each JSON.parse(data.content_text).answers as ansno, index (ansno)}
                {#if ansno.is_correct === "1"}
                  {#if answ[ansindex] == index}
                    <span class="activeans">{index + 1}</span>
                  {/if}
                  {#if answ[ansindex] != index}
                    <span class="disactiveans">{index + 1}</span>
                  {/if}
                {/if}
                {#if ansno.is_correct === "0"}
                  {#if answ[ansindex] == index}
                    <span class="actives">{index + 1}</span>
                  {/if}
                  {#if answ[ansindex] != index}
                    <span class="disactiveans">{index + 1}</span>
                  {/if}
                {/if}
              {/each}
            {/if}
          {/each}
        </td>
      </tr>
    {/each}
  </table>
  <div style="margin-top:3rem;height:1rem" />
{:else}
  <div class="body">
    {#each questData as data, i (data)}
      {#if i === indexno1}
        <a href="/" style="text-decoration: none;color:red"
          ><h3>Question :</h3>
          {JSON.parse(data.content_text).question}</a
        >
      {/if}
        {#if i === indexno1}
            {#each JSON.parse(data.content_text).answers as ansno, index (ansno)}
            
                    <br/>{index + 1} . {@html ansno.answer}
                  
            {/each}
        {/if}

      {#if i == indexno1}
        <hr />
        <p class="explanation">
          <i
            ><h3>Explanation :</h3>
            {@html escapedExplanation(i)}</i
          >
        </p>
      {/if}
    {/each}
  </div>
  <div class="footer">
    <p>{indexno1 + 1}/11</p>
    {#if indexno1 >= 1}
      <button on:click={() => previous()} style="margin-left :30rem"
        >Previous</button
      >
    {:else}
      <button
        on:click={() => previous()}
        disabled
        style="background-color:  rgba(230, 221, 220,0.8) !important;cursor:text; color:black !important;margin-left :30rem"
        >Previous</button
      >
    {/if}
    {#if indexno1 <= 9}
      <button on:click={() => next()}>Next</button>
    {:else}
      <button
        on:click={() => next()}
        disabled
        style="background-color:  rgba(230, 221, 220,0.8) !important;cursor:text; color:black !important;"
        >Next</button
      >
    {/if}
    <button
      ><a href="/" style="color: white;text-decoration:none">Go to Dashboard</a
      ></button
    >
  </div>
{/if}

<style>
  header {
    padding: 0.19em 0.1rem;
    display: flex;
    align-items: center;
    border-bottom: 2px solid crimson;
  }

  header h1 {
    margin: auto;
    font-family: "Overpass", sans-serif;
    font-size: 2.5rem;
    line-height: 20px;
    margin-top: 8px;
    color: crimson;
  }
  .img-fluid {
    width: 8rem;
    height: 3rem;
  }
  .bodycorrect {
    margin: 5rem 10rem;
    border: 3px solid crimson;
  }
  .bodycorrect div {
    font-size: 14px;
    display: inline-block;
    width: 12.26rem;
    text-align: center;
  }
  .total {
    border-bottom: none;
    text-align: center;
    color: red;
  }

  span {
    display: inline-block;
    width: 17px;
    height: 20px;
    font-weight: bold;
    border: 2px solid rgb(145, 136, 136);
    color: rgb(121, 109, 109);
    border-radius: 50%;
    margin: 0 0.2rem;
  }
  .activeans {
    border: 2px solid green;
    color: green;
  }
  .actives {
    border: 2px solid red;
    color: red;
  }
  /* .actives1{
        color:red;
    } */
  .disactiveans {
    border: 2px solid black !important;
    color: black !important;
  }
  .questArea {
    width: 50rem;
    margin: 0 3rem;
  }
  .ansArea {
    padding-left: 1rem;
    margin: 0 1.2rem;
    width: 8rem;
    text-align: center;
  }
  table {
    margin: auto;
    text-align: center;
  }
  tr {
    padding: 1rem 0;
  }
  p {
    color: blue;
    cursor: pointer;
  }
  .footer {
    position: relative;
    bottom: 2px;
    left: 0px;
    background-color: rgba(41, 36, 36, 0.788);
    height: 5rem;
    width: 100vw;
    margin-top: 10em;
  }
  .footer button {
    font-size: 20px;
    padding: 0.3rem 1rem;
    float: left;
    cursor: pointer;
    position: relative;
    margin-left: 2rem;
    border: none;
    background-color: crimson;
    color: white;
    outline: none;
    padding: 0.3rem 1rem;
    right: 0px;
  }
  .explanation {
    color: rgb(42, 154, 206);
  }
  .body {
    margin: auto;
    width: 60rem;
    border: 1px solid red;
    margin-top: 2rem;
    padding: 0rem 1.4rem;
  }
  .footer p {
    text-align: center;
    color: white;
  }
</style>
